.default {
    padding: 3px 10px;
    border: 2px solid #484848;
    border-radius: 5px;
}

.button {
    background-color: #FFFFFF;
    border: 2px solid #222222;
    border-radius: 10px;
    box-sizing: border-box;
    color: #222222;
    cursor: pointer;
    display: inline-block;
    font-family: Circular, -apple-system, BlinkMacSystemFont, Roboto, "Helvetica Neue", sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    margin: 0.5px 1px;
    outline: none;
    padding: 1px 7px;
    position: relative;
    text-align: center;
    text-decoration: none;
    touch-action: manipulation;
    transition: box-shadow .2s, -ms-transform .1s, -webkit-transform .1s, transform .1s;
    user-select: none;
    -webkit-user-select: none;
    width: auto;
}

.button:focus-visible {
    box-shadow: #222222 0 0 0 2px, rgba(255, 255, 255, 0.8) 0 0 0 4px;
    transition: box-shadow .2s;
}

.button:active {
    background-color: #F7F7F7;
    border-color: #000000;
    transform: scale(.96);
}

.button:disabled {
    border-color: #7c7c7c;
    color: #7c7c7c;
    cursor: not-allowed;
    opacity: 1;
}


.button:focus {
    box-shadow: inset 0 1px 0 -0.25px rgba(255, 255, 255, 0.2), 0 0.5px 1px rgba(0, 0, 0, 0.1), 0 0 0 4px rgba(58, 108, 217, 0.5);
    outline: 0;
}

.red {
    background: #ea6f6f;
}